.btn2{
    background: black;
    background-color: black;
}
.form-container h2{
    text-align: center;
}
.form-container form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}
.form-container input{
    margin-bottom: 1rem;
    border: 1px solid orange;
    padding: .5rem;
    border-radius: 1rem;
    color: orange;
    background-color: rgb(49, 49, 49);

}
.form-container input:hover{
    border: 1.1px solid rgb(176, 116, 5);
}
.input-container:nth-child(1){
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
}
.form-container textarea{
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 1px solid orange;
    padding: .5rem;
    border-radius: 1rem;
    background-color: rgb(49, 49, 49);
    color: orange;
}
.first-inp{
    margin-right: 5rem;
}

