.contact-section{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 2rem;
}
.contact-section img{
    margin-left: 2rem;
}

.form-content h3{
    text-align: center;

}
@media (max-width:854px) {
    .contact-section img{
        width: 15rem;
    }
    
}

@media (max-width:772px) {
    .contact-section img{
        width: 10rem;
    }
    .form-container input{
        width: 10rem;
    
    }   
}

@media (max-width:619px) {
    .contact-section img{
        width: 7rem;
    }
    .form-container input{
        width: 7rem;
    
    }   
}

@media (max-width:459px) {
    .contact-section img{
        width: 7rem;
    }
    .form-container input{
        width: 7rem;
    
    }   
    .contact-section img{
        display: none;
    }
}